@use "./variables";

$breakpoints: (
  "phone": (
    min-width: 320px,
  ),
  "phone-m": (
    min-width: 420px,
  ),
  "tablet": (
    min-width: 750px,
  ),
  "tablet-l": (
    min-width: 992px,
  ),
  "desktop": (
    min-width: 1200px,
  ),
  "desktop-s": (
    min-width: 1300px,
  ),
  "desktop-m": (
    min-width: 1440px,
  ),
  "desktop-l": (
    min-width: 1740px,
  ),
  "desktop-xl": (
    min-width: 1920px,
  ),
) !default;

@mixin media($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    @media #{inspect(map-get($breakpoints, $breakpoint))} {
      @content;
    }
  } @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
      + "Available breakpoints are: #{map-keys($breakpoints)}.";
  }
}

/*
 * Custom media query - any breakpoint
 * @param $media - Breakpoint with all the format, ex: 'max-width: 992px' or 'max-width: 992px'
*/
@mixin mediaCustom($media) {
  @media ($media) {
    @content;
  }
}

/*
 * Reset properties
*/
@mixin resetProperties() {
  margin: unset;
  padding: unset;
  border: unset;
  background: unset;
  border-radius: unset;
}

/*
 * Reset properties
 * @param $time - time for transition
 * @param $effect - Effect with :active
*/
@mixin clickEffect($time: 0.3s, $effect: scale(0.98)) {
  cursor: pointer;
  transition: transform $time;

  &:active {
    transform: $effect;
  }
}

/*
 * Diplay flex
*/
@mixin boxFlex($justify: center, $align: center, $direc: row) {
  align-items: $align;
  display: flex;
  justify-content: $justify;
  flex-direction: $direc;
}

/*
 * Diplay grid
*/
@mixin boxGrid(
  $columns: repeat(3, 1fr),
  $rows: repeat(3, 1fr),
  $gap-column: 5px,
  $gap-row: 0
) {
  display: grid;
  grid-template-columns: $columns;
  grid-template-rows: $rows;
  column-gap: $gap-column;
  row-gap: $gap-row;
}

/*
 * Underline effect of the two forms.
 * @param $lineColor - Color of the underline
 * @param $lineHeight - Weight of the underline
 * @param $double - If true: underline be disappeared with hover
 * else: underline be appeared with hover
*/
@mixin linkEffect($lineColor: currentColor, $lineHeight: 2px, $double: true) {
  display: inline-block;
  position: relative;
  @include clickEffect;

  &:after {
    height: $lineHeight;
    width: 100%;
    position: absolute;
    bottom: 2px;
    left: 0;
    background-color: $lineColor;
    content: "";
    transition: transform 0.3s ease-out;

    @if $double == false {
      transform: scaleX(0);
      transform-origin: bottom right;
    } @else {
      transform: scaleX(1);
      transform-origin: bottom left;
    }
  }

  &:hover:after {
    @if $double == false {
      transform: scaleX(1);
      transform-origin: bottom left;
    } @else {
      transform: scaleX(0);
      transform-origin: bottom right;
    }
  }
}

/*
 * Custom color for the button
 * @param $color - font color
 * @param $bg - background color
 * @param $border - border color
*/
@mixin btn-type($color, $bg, $border) {
  color: $color;
  background-color: $bg;
  border: 1px solid $border;
}

/**
 * Create excerpt
 * @param - $lines - Lines to show
*/
@mixin excerptForLine($lines) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@mixin marginsInside() {
  margin-left: calc(0rem - variables.$paddings-side--movil);
  margin-right: calc(0rem - variables.$paddings-side--movil);
  padding: 0 variables.$paddings-side--movil;
  overflow-x: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

/*
* Scroll styles
* Soport: Chrome, Safari, Brave, Mozilla, etc
* @param - $barColor - default: $primary - Bar color in the scroll
* @param - $barWidthX - default: 5px - Bar width
* @param - $barWidthY - default: 5px - Bar height
*/
@mixin scroll($barColor: variables.$color-principal,
  $barWidthX: 5px,
  $barWidthY: 5px,
  $barBg: variables.$color-white) {
  scrollbar-color: $barColor;
  scrollbar-width: $barWidthX;

  &::-webkit-scrollbar {
    margin-top: 5rem;
    padding-top: 5rem;
    height: $barWidthX;
    width: $barWidthY;
  }

  &::-webkit-scrollbar-track {
    border-radius: 6px;
    background-color: $barBg;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: $barColor;
  }
}
