/* ====== Layout ====== */

$max-width--wrapper: 1440px;
$max-width--wrapper-m: 1048px;
$max-width--wrapper-s: 900px;
$max-width--wrapper-xs: 712px;

$paddings-side-xl: 28.813rem;
$paddings-side-l: 23.125rem;
$paddings-side-xm: 22.75rem;
$paddings-side-m: 11.25rem;
$paddings-side-s: 3.125rem;
$paddings-side--movil: 1rem;

$margin-section: 60px;
$margin-section--movil: 50px;

$margin-section--hero: 24px;
$margin-section--hero-movil: 0;

$margin-xs: 8px;
$margin-s: 12px;
$margin-m: 16px;
$margin-l: 24px;
$margin-xl: 32px;
$margin-xxl: 40px;

/* ====== Font ====== */

// Body and Body - Upper
$font-family--general: Poppins, sans-serif; // Poppins (Inter)

// Lowercase
$font-size--general-xs: 10px;
$line-height--general-xs: 14px;
$font-size--general-s: 12px;
$line-height--general-s: 17px;
$font-size--general-m: 14px;
$font-size--general-m-major: 16px;
$line-height--general-m: 24px;
$font-size--general-l: 17px;
$line-height--general-l: 28px;
$line-heigh--general-l: 28px;

// Uppsercase
$font-size--general-up-s: 13px;
$line-height--general-up-s: 20px;
$font-size--general-up-m: 15px;
$font-size--general-up-l: 18px;

// Weight
$font-light: 300;
$font-regular: 400;
$font-medium: 500;
$font-semibold: 600;

// Headings
$font-family--title: 'Larken', sans-serif; // Larken

// Mobile
$font-size--mobile-h1: 30px;
$line-height--mobile-h1: 44px;
$font-size--mobile-h2: 28px;
$line-height--mobile-h2: 40px;
$font-size--mobile-h3: 26px;
$line-height--mobile-h3: 36px;
$font-size--mobile-h4: 24px;
$line-height--mobile-h4: 32px;
$font-size--mobile-h5: 22px;
$line-height--mobile-h5: 28px;
$font-size--mobile-h6: 18px;
$line-height--mobile-h6: 24px;

$font-size--mobile--h1-customer: 25px;
$line-height--mobile--h1-customer: 34px;

// Desktop

$font-size--desk-h1: 40px;
$line-height--desk-h1: 53px;
$font-size--desk-h2: 38px;
$line-height--desk-h2: 60px;
$font-size--desk-h3: 34px;
$line-height--desk-h3: 48px;
$font-size--desk-h4: 28px;
$line-height--desk-h4: 40px;
$font-size--desk-h5: 24px;
$line-height--desk-h5: 36px;
$font-size--desk-h6: 18px;
$line-height--desk-h6: 32px;

/* ====== Colors ====== */
$color-principal: var(--color-base-principal);
$color-principal--variant-1: #0154D1;
$color-principal--variant-2: #0049B7;
$color-principal--variant-3: #0066FF;
$color-secondary: var(--color-base-secondary);

$color-accent-1: var(--color-base-accent-1);
$color-accent-2: var(--color-base-accent-2);
$color-accent-3: var(--color-base-accent-3);

$color-text: var(--color-base-text);
$color-text-2: #474D66;
$color-title: var(--color-base-title);
$color-link: var(--color-base-link);

$color-background: var(--color-base-bg);
$color-background-2: var(--color-base-bg-2);
$color-background-3: #8F95B2;
$color-background-4: #D8DAE5;
$color-background-5: #EDEFF5;
$color-background-6: #CFE5FF;

$color-success: #428445;
$color-error: $color-accent-3;
$color-newsletter-error: $color-accent-3;
$color-newsletter-error-bg: #ff670144;
$color-newsletter-success: #338536;
$color-newsletter-success-bg: #4caf4f41;

$color-black: #000000;
$color-light-black: #28334E;
$color-white: #FFFFFF;
$area-menu-column: var(--template-drop-columns);

/* ====== Grid ====== */
$gap-s: 9px;
$gap-m: 16px;
$gap-l: 20px;
$gap-xl: 27px;
