@use "../abstracts/variables";
@use "../abstracts/mixins";

.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 4;
}

.cart {
  &#side-cart {
    display: block;
  }

  &.upsell-vertical {
    display: flex;
    width: 630px;

    .vertical__upsell {
      width: 130px;
      height: 100%;
      overflow: hidden;

      .cart-upsell,
      #upsell-js,
      #cart-upsell-slider,
      #slider_side-cart {
        height: 100%;
        .cart-upsell-container-product {
          width: 100%;
        }
      }
    }
    .cart-wrapper {
      width: 500px;
    }
  }


  .cart-upsell-title.h6 {
    @include mixins.media('tablet-l') {
      font-size: 20px;
    }
  }

  visibility: hidden;
  opacity: 0;
  height: 100%;
  width: 375px;
  position: fixed;
  right: 0;
  top: 0;
  background-color: variables.$color-white;
  z-index: 4;
  transform: translateX(400px);
  transition: all 0.4s ease;

  @include mixins.mediaCustom("max-width: 480px") {
    width: 100%;
  }

  @include mixins.media("desktop-l") {
    width: 450px;
  }

  &-header__title--text.h5 {
    font-size: variables.$font-size--mobile--h1-customer;
    line-height: variables.$line-height--mobile--h1-customer;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--mobile-h1;
      line-height: variables.$line-height--mobile-h1;
    }
  }

  &[data-active="true"] {
    opacity: 1;
    visibility: visible;
    transform: translateX(0px);
  }

  &__max-quantity {
    position: fixed;
    left: 50%;
    bottom: -15%;
    z-index: 10000;
    width: max-content;
    max-width: calc(100% - 32px);
    padding: 15px 16px;
    border: 1px solid variables.$color-error;
    transform: translateX(-50%);
    transition: bottom 0.3s ease-in-out;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.15);
    text-align: center;
    background-color: variables.$color-white;
    color: variables.$color-error;

    &[data-active="true"] {
      bottom: 20px;
    }
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;

    & > * {
      padding-top: 10px;
      padding-bottom: 10px;
    }

    & > *:first-of-type {
      border: 0;
    }
  }

  &-margins__inline {
    margin: 0 auto;
    width: 91.5%;

    @include mixins.media("desktop-l") {
      width: 93%;
    }
  }

  &-close {
    width: 13px;
    height: 13px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-header {
    padding: 14px 19px;
    background-color: #fff;
    z-index: 1;
    @include mixins.boxFlex(space-between, center, row-reverse);

    .cart-close {
      cursor: pointer;
    }

    &__title--text {
      color: variables.$color-black !important;
    }
  }

  .items-container {
    overflow-y: auto;
    border-top: 1px solid variables.$color-background-4;
    height: 100%;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &-items {
    height: auto;

    &__container {
      display: grid;
      grid-template-columns: 78px auto;
      grid-column-gap: 10px;
      margin: 0 0 variables.$margin-m;
      color: variables.$color-black;
      --loading-color: variables.$color-black;

      &--title,
      &--condition,
      .item-price {
        font-size: variables.$font-size--general-s;
        line-height: variables.$line-height--general-s;
      }

      &--title span,
      &--product-title {
        color: variables.$color-black;
      }

      &--title span {
        @include mixins.excerptForLine(2);
      }

      &--product-title {
        position: relative;
        display: block;
      }

      .wk-button {
        padding: 1px !important;
      }

      .wk-icon__svg {
        height: 13px !important;
      }

      &--product-info {
        position: relative;
        @include mixins.boxFlex(center, flex-start, column);

        .item-price {
          display: flex;
          margin-top: 6px;
        }

        .item-delete,
        .item-quantity button {
          cursor: pointer;
        }
        .item-quantity {
          display: none;
        }
      }

      .item-delete,
      &--condition {
        position: absolute;
        right: 0;
      }

      .compare-price,
      &--condition {
        color: variables.$color-background-3;
      }

      .compare-price {
        margin-right: variables.$margin-xs;
        text-decoration: line-through;
      }

      .item-delete {
        top: 5px;
      }
      .item-delete svg {
        width: 14px;
        height: 15px;
      }

      &--condition {
        bottom: 0;
      }

      &--image {
        width: 100%;
        height: 70px;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    &__empty {
      display: block;
      padding: 110px 0 40px;

      &-wrapper {
        min-height: 380px;
        padding: 0;
        text-align: center;
        color: variables.$color-principal;
      }

      &-link {
        margin: 0 auto;
      }
    }

    &__complement {
      display: block;
      margin-bottom: 20px;
    }
  }

  &-upsell {
    margin-top: auto;

    &-title {
      font-weight: variables.$font-regular !important;
    }

    &-title,
    #upsell-js {
      padding-right: 19px;
      padding-left: 19px;
    }

    #upsell-js {
      background-color: variables.$color-secondary;
    }

    #upsell-js &__slider {
      padding: variables.$margin-xs 0;
    }

    .swiper-pagination {
      margin-top: 15px;

      &-bullet {
        height: 5px;
        width: 5px;
        border: none;
        background-color: variables.$color-background;
      }

      &-bullet-active {
        background-color: variables.$color-principal;
      }

      &-bullet:not(:first-child) {
        margin-left: 4px;
      }

      &:has(.swiper-pagination-bullet:only-child) {
        display: none;
      }
    }

    .wrapper__slider button.swiper-button {
      display: none;
      color: variables.$color-principal;
      height: 40px;
      width: 40px;

      @include mixins.media('tablet') {
        display: block;
      }


      svg {
        height: 100%;
        width: 100%;
      }
    }

    .wrapper__slider button.swiper-button-prev {
      left: -30px;

      @include mixins.media('tablet') {
        left: -29px;
      }
    }

    .wrapper__slider button.swiper-button-next {
      right: -30px;

      @include mixins.media('tablet') {
        right: -29px;
      }
    }

    &-container-product {
      height: 78px;
      @include mixins.boxFlex();

      /*
      Makes the slider not responsive

      @include mixins.media("phone-m") {
        width: 337px !important;
      }
      @include mixins.media("desktop-l") {
        width: 412px !important;
      }
      */

      .upsell-btn {
        padding: 10px;
        width: 100%;
      }

      &__image {
        width: 78px;
        height: 78px;
        flex-shrink: 0;

        img {
          height: 100%;
        }
      }

      &__mobile-gropup {
        width: 74%;
        margin-left: 8px;
        @include mixins.boxFlex(space-between, center, row);

        @include mixins.media("desktop-l") {
          width: 79%;
        }
      }

      &__info {
        color: variables.$color-black;
        @include mixins.boxFlex(center, flex-start, column);
        margin-right: 8px;

        &-title,
        &-compare,
        &-price {
          font-size: variables.$font-size--general-s;
          line-height: variables.$line-height--general-s;
        }

        &-title {
          color: variables.$color-black;
          @include mixins.excerptForLine(2);
          height: 34px;
        }

        &-compare {
          color: variables.$color-background-3;
          margin-right: variables.$margin-xs;
          text-decoration: line-through;
        }
      }

      &__btn .btn-add-to-cart {
        width: fit-content;
        padding: 5px 8px;
        font-size: variables.$font-size--general-xs;
        line-height: variables.$line-height--general-xs;
        min-width: 72px;
      }
    }
  }

  #discount-validation {
    &[data-active="false"] {
      display: none;
    }
  }

  &-discount {
    padding: 10px variables.$paddings-side--movil 0;

    &[data-state="true"] {
      .cart-discount__form {
        display: none;
      }
      .cart-discount__success {
        display: block;
      }
    }

    &[data-state="false"],
    &[data-state="error"] {
      .cart-discount__success {
        display: none;
      }
    }

    &[data-state="error"] {
      .cart-discount__error {
        display: block;
      }
      .cart-discount__info {
        display: none;
      }
      input {
        border-color: variables.$color-error !important;
      }
    }

    .cart-discount__error,
    .cart-discount__empty {
      display: none;
      color: variables.$color-error;
    }
    &__form-inputs {
      @include mixins.boxFlex(space-between, flex-start, column);
      gap: 8px;
      @include mixins.mediaCustom("min-width: 360px") {
        @include mixins.boxFlex(space-between, flex-end, row);
      }
    }

    &__apply,
    &__input input {
      height: 40px;
      font-size: variables.$font-size--general-up-s;
      outline: none;
    }
    &__apply {
      min-width: 96px;
      padding: 10px 18px;
      width: 38%;
      max-width: 122px;
    }
    &__input {
      width: 100%;

      @include mixins.mediaCustom("min-width: 360px") {
        width: 70%;
      }

      label {
        display: block;
        margin-bottom: variables.$margin-xs;
        font-size: variables.$font-size--general-s;
      }

      input {
        border-radius: 0;
        width: 100%;
        padding: 10px 12px;
        border: 1px solid variables.$color-background;
        margin-right: 10px;

        @include mixins.mediaCustom("min-width: 360px") {
          width: 100%;
          max-width: 100%;
        }
        @include mixins.media("desktop-l") {
          max-width: 280px;
        }
      }

      .input-container label {
        display: none;
      }
    }
    &__info,
    &__error,
    &__empty {
      font-size: variables.$font-size--general-s;
      display: block;
      margin-top: 6px;
    }

    &__success {
      p,
      input {
        font-size: variables.$font-size--general-up-s;
        line-height: variables.$line-height--general-up-s;
      }

      &[data-active="false"] {
        display: none;
      }

      label {
        display: block;
        margin: variables.$margin-xs 0;
        font-size: variables.$font-size--general-s;
        line-height: variables.$line-height--general-up-s;
        color: variables.$color-background-3;
      }

      button {
        float: right;
        width: fit-content;
        padding: 5px 8px;
        font-size: variables.$font-size--general-xs;
        line-height: variables.$line-height--general-xs;
      }

      button:first-of-type {
        margin-left: 6px;
      }

      input {
        width: 100%;
        height: 40px;
        margin: 0 0 variables.$margin-s;
        padding: 10px 12px;
        border: 1px solid #000;
        background-color: variables.$color-white;
        color: #000;
      }

      input:disabled {
        border: 1px solid #000;
        color: #000;
      }
    }

    .cart-discount__value {
      margin-top: 34px;
      @include mixins.boxFlex(space-between, center, row);

      #total-discount {
        @include mixins.boxFlex(flex-end, center, row);
      }
    }

    .cart-discount__success-btn-2 {
      min-width: 128px;

      &:hover {
        color: variables.$color-white;
        background-color: variables.$color-principal;
      }

      & > .loading {
        width: 14px;
        height: 14px;
        border-top-color: variables.$color-white;
      }
    }
  }

  &-footer {
    text-align: center;
    position: sticky;
    bottom: 0;
    background-color: #fff;
    z-index: 1;

    &__shipping {
      display: none;
      text-align: start;
      margin-bottom: 6px;
    }

    &__button {
      &--btn {
        width: 100%;
      }
      .animation {
        display: none;
      }
    }
  }

  #container-footer-js {
    padding: 0;

    &:empty {
      display: none;
    }

    .cart-margins__inline {
      padding-bottom: 10px;
      padding-top: 16px;
    }
  }
}

#side-cart:has(.cart-items__empty-wrapper) {
  .cart-discount {
    display: none;
  }
}

#side-cart {
  .item-delete span,
  .item-price .item-line-price {
    display: none;
    text-transform: uppercase;
  }
}

.btn-add-to-cart .loading {
  border-top-color: variables.$color-white;
}

.btn-add-to-cart:hover {
  .loading {
    border-top-color: variables.$color-principal;
  }

  &.btn-checkout .loading {
    border-top-color: variables.$color-principal;
  }
}
